const AboutMeDiv = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 80%;
	margin: auto;
	min-height: 600px;
`;

export default function AboutScreen() {
	return (
		<>
			<Container id="about-scroll" fluid>
				<AboutMeDiv>
					<Row className="m-auto">
						<Col md={6}>
							<div className="bg-warning text-dark p-3  rounded">
								<h2 className="text-center">📋 Mis metas</h2>
								<p className="text-center fw-bolder">
									● Convertirme en software development. <br />
									● Trabajar en una empresa y/o startup. <br />
									● Enseñar mis conocimientos al resto de personas. <br />
									● Ser un gran aporte en donde quiera que trabaje. <br />●
									Aprender y seguir creciendo en este ambiente.
								</p>
							</div>
						</Col>
						<Col md={6}>
							<div className="bg-info text-dark p-3 rounded">
								<h2 className="text-center">✅ Mi objetivos?</h2>
								<p className="fw-bolder text-center">
									Aportar valor a la empresa de la que formo parte, dar ideas y
									crecer en un equipo fuerte. Creo que algo que puedo aportar es
									mi buena actitud frente a las situaciones y a su vez ser
									alguien creativo que siempre busca soluciones a los problemas,
									asimismo, me gusta ser responsable y respetuoso, siempre
									buscando tener el mejor ambiente laboral posible.
								</p>
							</div>
						</Col>
						<Col md={12}>
							<div className="bg-success text-dark p-4 rounded mt-4">
								<h2 className="text-center">💭 Extra</h2>
								<p className="fw-bolder text-center">
									Soy un estudiante de ingeniería en informática, una de las
									cosas que más me llama la atención es que me gusta ser
									autodidacta y estar actualizado de las nuevas tecnologías en
									el mercado. Actualmente me encuentro cursando el cuarto año de
									mi carrera y estudiando por mi cuenta las siguientes
									tecnologias <b>M-E-R-N</b>, es decir; <b>MongoDB,</b> <b>Express, </b>
									<b>ReactJS, </b> <b>Node.js.</b>
								</p>
								<p className="fw-bolder text-center te">
									Aún no tengo la experiencia suficiente en el área laboral y/o
									empresarial por lo mismo me esfuerzo cada día en ampliar mis
									conocimientos, espero que cuando llegue el momento de trabajar
									en alguna empresa poder crecer como persona junto con la
									empresa de forma en que las 2 partes ganemos valor.
								</p>
							</div>
						</Col>
					</Row>
				</AboutMeDiv>
			</Container>
		</>
	);
}
