import React, { useState } from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-scroll";
import styled from "styled-components";

const NavBar = styled(Navbar)`
	height: 90px;
	width: 100%;
	border: none;
	background: rgba(255, 255, 255, 0.3);
	box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.4);
	backdrop-filter: blur(1.8px);
	-webkit-backdrop-filter: blur(1.5px);
	transition: 300ms;
`;

const LinkStyled = styled(Link)`
	text-decoration: none;
	cursor: pointer;
	width: 125px;
	font-size: 100%;
	margin-right: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 20px;
	color: white;

	&:hover {
		background-color: gold;
	}

	@media (max-width: 768px) {
		display: none;
	}
`;

const Title = styled.h3`
	color: white;
	font-weight: bold;
	padding: 5px;
	cursor: pointer;
	border-bottom: 2px solid white;
`;

export default function Header() {
	const [navbarbg, setNavbarBg] = useState(false);
	const changeBackground = () => {
		if (window.scrollY >= 80) {
			setNavbarBg(true);
		} else {
			setNavbarBg(false);
		}
	};

	window.addEventListener("scroll", changeBackground);
	return (
		<NavBar
			className={
				navbarbg ? "bg-transparent" : "navbar navbar-expand-lg bg-black"
			}
			fixed="top"
		>
			<Container className="d-flex justify-content-between mx-4" fluid>
				<Link to="intro-scroll" smooth={true} duration={1000} offset={-300}>
					<Title>Shacosu.js</Title>
				</Link>
				<Nav className="d-flex justify-content-between">
					<Link
						to="contact-scroll"
						smooth={true}
						offset={-200}
						duration={1000}
						className="bg-white rounded-pill text-decoration-none p-2 mx-2"
						role="button"
					>
						<i className="fas fa-envelope mx-1 py-2"></i> Contacto
					</Link>
					<LinkStyled
						to="about-scroll"
						smooth={true}
						duration={1000}
						offset={-250}
						className="mx-2"
					>
						<i className="fas fa-info-circle mx-1"></i> Sobre Mi
					</LinkStyled>

					<LinkStyled
						to="proyect-scroll"
						smooth={true}
						offset={-200}
						duration={1000}
						className="mx-2"
					>
						<i className="fas fa-bookmark mx-1"></i> Proyectos
					</LinkStyled>
				</Nav>
			</Container>
		</NavBar>
	);
}
