import React from "react";
import { Row, Col, Container } from "react-bootstrap";

export default function Footer() {
	return (
		<Container className="mb-4 sticky-bottom fw-bolder" fluid>
			<Row className="bg-transparent">
				<Col md={6} className="text-center text-white p-2">
					<label className="fw-bolder">
						<i className="fas fa-clock"></i> Horarios Disponibles
					</label>
					<p className="mt-1 m-0">- Lunes a Viernes - 13:00 a 19:00</p>
					<p className="py-2 mt-2 w-25 mx-auto border-bottom">
						@Shacosu -{" "}
						<a
							href="https://github.com/Shacosu"
							target="_blank"
							rel="noreferrer"
						>
							<i className="fab fa-github text-white"></i>
						</a>
					</p>
				</Col>
				<Col md={6}>
					<Row className="w-100 p-2 h-75">
						<label className="text-center text-black fw-bolder text-white mb-2">
							<i className="fas fa-envelope"></i> Redes Sociales
						</label>
						<div className="d-flex mx-auto justify-content-around align-items-center w-50 h-100">
							<div className="text-center ">
								<a
									href="https://www.instagram.com/4notf0und4"
									target="_blank"
									rel="noreferrer"
									className="my-1 btn border-bottom text-white socialHover"
								>
									<i className="fab fa-instagram"></i> Instagram
								</a>
							</div>
							<div className="text-center">
								<a
									href="https://www.facebook.com/shaco.fox"
									target="_blank"
									rel="noreferrer"
									className="my-1 btn border-bottom text-white socialHover"
								>
									<i className="fab fa-facebook"></i> Facebook
								</a>
							</div>
						</div>
					</Row>
				</Col>
			</Row>
		</Container>
	);
}
